<template>
  <div class="concert">
    <list-title>热歌-飙升榜</list-title>
    <Loading v-if="conList.length< 1"></Loading>
    <div class="top">
      <div class="left" >

        <p @click="conList=concert_hot" :class="{active:conList == concert_hot}">热歌榜</p>
        <p @click="conList=concert_forw" :class="{active:conList == concert_forw}">飙升榜</p>

      </div>

      <div class="right" @click="$emit('addList',conList)"> 
        <concert-list @playing="$emit('playing',$event)" v-for="(sing,i) in conList" :key="i" :sing="sing" :i="i"></concert-list>
      </div>
    </div>
<div class="content">
  <div class="new_al">
    <list-title>最新专辑</list-title>
    <p @click="toggleNew" >
        <i>换一下</i>
        <img :class="{rotate:rotate}" src="../assets/img/toggle.png" alt="">
      </p>
      </div>
    <div class="daily" v-for="song in slice_list" 
       :key="song.id" >
      <router-link tag="div" :to="`/recommend_songs/${song.id}`"> 
      <pic-text 
       :song="song"
     
       ></pic-text>
       </router-link>
      
    </div>
  <div class="new_al">
    <list-title>你喜欢的歌单</list-title>
    <p @click="toggleLike" >
        <i>换一下</i>
        <img :class="{rotate:rotate}" src="../assets/img/toggle.png" alt="">
      </p>
      </div>

    <div class="like">
            <pic-list v-for="item in sliceLike" :key="item.id" :item="item"></pic-list>
    </div>

</div>

  </div>
</template>

<script>
import ListTitle from "@/components/ListTitle";
import PicList from "@/components/PicList";
import PicText from "@/components/PicText";
import Loading from "@/components/Loading";
import ConcertList from "@/components/ConcertList";


export default {
  name: 'Concert',
  components: {
  ListTitle,
  PicList,
  PicText,
  Loading,
  ConcertList
    

  },
   data(){
    return{
        concert_like:[],
        daily_list:[],
        albums_list:[],
        conList:[],
        concert_hot:[],
        concert_forw:[],
        listId:"",
        startLike:6,
        sliceLike:[],
        start: 3,
        slice_list:[],
        rotate:false
    }
  },
  
 created(){
     // 喜欢的歌单
      this.$axios.get("/top/playlist").then((data)=>{    
        this.concert_like = data.data.playlists.map(item => {
        item.picUrl = item.coverImgUrl;
       return item;
        })
        this.sliceLike =  this.concert_like.slice(0,6)
        // console.log(this.concert_like)
      });
      // 最新专辑
      this.$axios.get("/album/newest").then((d=>{
      this.albums_list = d.data.albums.map(song=> {
        song.artists = song.company;
        return song;
      });
        // console.log(this.albums_list)
      this.slice_list= this.albums_list.slice(0,3);

    }));
// 热歌榜
       this.$axios.get("playlist/detail?id=3778678").then((data) => {
        this.concert_hot = data.data.playlist.tracks.slice(0,6)
        this.conList = this.concert_hot;
    //  console.log(this.concert_hot);
   });
// 飙升榜 
       this.$axios.get("playlist/detail?id=19723756").then((data) => {
        this.concert_forw = data.data.playlist.tracks.slice(0,6)

   });

  },
  methods: {
    // 切换数据
    toggleNew(){
      let end = this.start + 3;
      this.rotate = true   
      if (end < 13) {
        this.slice_list = this.albums_list.slice(this.start, end);
      } else {
        this.start = 0;
        end = 3;
        this.slice_list = this.albums_list.slice(this.start, end);
      }
      this.start = end;

      // 让rotate数据500ms后改为false
      setTimeout(()=>{
        this.rotate = false;
      },500)
    },
    toggleLike(){
      let endLike = this.startLike + 6;
      this.rotate = true   
      if (endLike < 49) {
        this.sliceLike = this.concert_like.slice(this.startLike, endLike);
      } else {
        this.startLike = 0;
        endLike = 6;
        this.sliceLike = this.concert_like.slice(this.startLike, endLike);
      }
      this.startLike = endLike;

      // 让rotate数据500ms后改为false
      setTimeout(()=>{
        this.rotate = false;
      },500)
    },
  }

}
</script>

